export default {
	name: 'GetOpenId',
	props:{
		isNext:{
			type: String,
			default: '0'
		}
	},
	data() {
		return {
			appid: 'wx5a7e48b2d2c7cc4b', //这里是我的appid，需要改成你自己的
			secret: '9fdc700fef21bb7d3141a50f3fc82591' //密钥也要改成你自己的
		};
	},
	methods: {
		getOpenId() {
			const self = this;
			uni.login({
				success: function(res) {
					if (res.code) {
						uni.getUserInfo({
							success: function(res) {
								console.log('存在code');
							}
						});
						var appid = self.appid; //这里是我的appid，需要改成你自己的
						var secret = self.secret; //密钥也要改成你自己的
						var openid = '';
						var url = 'https://api.weixin.qq.com/sns/jscode2session?appid=' + appid + '&secret=' + secret + '&js_code=' +
							res.code + '&grant_type=authorization_code';
						uni.request({
							url: url,
							data: {},
							method: 'GET',
							success: function(res) {
								var obj = {};
								obj.openid = res.data.openid;
								console.log('openid: ' + res.data.openid);
								uni.setStorage({
									key: 'openId',
									data: obj.openid,
									success:res=>{
										if(self.isNext == '1'){
											uni.navigateTo({
												url: '../../pages/bevip/bevip'
											})
										} else if( self.isNext == '2'){
											uni.showLoading({
												title: '正在获取数据...',
											})
											uni.navigateTo({
												url:'../../pages/my/my',
												success:res=>{
													uni.hideLoading()
												}
											})
										}
									}
								})
								obj.expires_in = Date.now() + res.data.expires_in;
							}
						});
					} else {
						console.log('获取用户登录态失败！' + res.errMsg);
					}
				}
			})
		}
	}
}
